  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

:root {
  --little-round-border: 5px;
  --light-green: rgba(78, 255, 55, 0.3);
  --yellow: rgb(255, 251, 0);
  --light-red: rgba(255, 89, 89, 0.582);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  letter-spacing: 2px;
}

#root,
#__next {
  isolation: isolate;
}

/* MY STUFF BELOW */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  width: 100vw;
  height: 100vh;
}

.content {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template: 4.2rem 1fr 40px / 250px 1fr;
}

h1 {
  display: inline;
  position: absolute;
  left: 2vw;
  color: black;
}

h1 a {
  visibility: hidden;
}

h1:hover a {
  visibility: visible;
  color: rgb(0, 0, 0);
}

/* HEADER */

header {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: end;
  grid-column: 1 / 3;
  grid-row: 1 / 2;
  background-color: rgba(250, 243, 147, 0.705);

  /*
  height: 3.7rem;
  width: 100vw;
  */
}

header span {
  justify-self: flex-start;
}

header nav ul {
  list-style: none;
}

.menu-icon {
  padding: 0.7rem;
  cursor: pointer;
}

/* MAIN CONTENT */

.main-content {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
}

.main-content > :first-child {
  margin-top: 2rem;
}

.todo-item {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  width: 90%;
  margin-left: 5%;
  margin-right: 5%;
  margin-bottom: 5px;
  height: 2.3rem;
}

.todo-item:hover {
  background-color: rgb(233, 233, 233);
}

.todo-priority-low {
  margin-right: 5px;
  width: 3px;
  height: 100%;
  background-color: var(--light-green);
}

.todo-priority-medium {
  margin-right: 5px;
  width: 3px;
  height: 100%;
  background-color: var(--yellow);
}

.todo-priority-high {
  margin-right: 5px;
  width: 3px;
  height: 100%;
  background-color: var(--light-red);
}

.todo-checkbox {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-around;
}

.todo-checkbox input[type="checkbox"] {
  width: 15px;
  height: 15px;
  border: 1px solid black;
  border-radius: 10px;
  transition: box-shadow 0.3s;
  background: white;
}

.todo-description-button {
  margin-left: 10px;
  margin-right: 10px;
}

.todo-description-button > img {
  height: 20px;
}

.todo-delete-button {
  margin-left: auto;
}

.todo-delete-button > img {
  height: 20px;
}

.todo-title {
  margin-right: 5px;
}

.todo-date {
  margin-right: 20px;
}

/* SIDEBAR */

.sidebar {
  position: relative;
  grid-column: 1 / 2;
  grid-row: 2 / 4;
  font-size: 22px;
  background-color: #eee;
  border-right: 1px solid rgb(124, 124, 124);
  padding: 1.5rem;
  overflow: hidden;
}

.sidebar:hover {
  overflow: auto;
}

.sidebar {
  scrollbar-width: none; /* Firefox */
}

.sidebar ul {
  list-style: none;
}

.sidebar-navegation-menu ul {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.sidebar-navegation-menu > ul > * {
  padding: 0.5rem 1.5rem;
  margin-bottom: 7px;
  cursor: pointer;
}

.fa-hashtag {
  background-size: 0.3rem;
  font-weight: 1;
}

.project-item {
  padding: 0.1rem 0.8rem;
}

.project-item:hover {
  font-weight: 600;
}

.create-todo-icon {
  position: fixed;
  bottom: 32px;
  left: 45px;
  transform: translateX(-50%);
  font-size: 48px;
  color: #ffe96c;
  background-color: #000000;
  border-radius: 50px;
  padding: -1px;
  cursor: pointer;
}

/* FOOTBAR */

footer {
  grid-column: 1 / 3;
  grid-row: 3 / 4;
}

footer p {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

footer p a {
  text-decoration: none;
}

/* Forms for new todos and projects */

#create-new-window {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 9998;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex; /* Don't forgot to take away */
  align-items: center;
  justify-content: center;
}

.create-new {
  display: grid;
  grid-template: 3rem 1fr / clamp(0px, 20%, 150px) auto;
  background: white;
  padding: 0;
  width: clamp(20vw, 600px, max(90vw, 55rem));
  min-height: 40vh;
  border-radius: 5px;
}

.create-new-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: clamp(8px, 3vw, 15px);
  grid-row: 1 / 2;
  grid-column: 1 / 3;
  background-color: rgba(250, 243, 147, 0.705);
  font-size: 1.5rem;
  font-weight: 600;

  min-width: 0 !important; /* Permite que o flex container encolha */
  width: 100%; /* Garante que use toda largura disponvel */
  overflow: hidden;
}

.create-new-header > * {
  min-width: 0;
}

.create-new-header p {
  white-space: normal;
  word-break: break-word;
  /* ou para ellipsis quando muito pequeno */
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  flex-shrink: 1;
  min-width: 0; /* CRUCIAL para flex items com texto */
}

.create-new-header button {
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  font: inherit;
  color: inherit;
  cursor: pointer;
}

.create-new-sidebar {
  grid-row: 2 / 3;
  grid-column: 1 / 2;
  padding: 1.3rem;
  font-size: 1.2rem;
  background-color: #eee;
}

.create-new-options {
  display: flex;
  flex-flow: column wrap;
  justify-content: start;
  align-items: start;
  gap: 10px;
  margin-left: 20%;
}

.create-new-options li {
  cursor: pointer;
}

.create-new-sidebar ul {
  list-style: none;
}

.create-new-sidebar > ul > *:hover {
  font-weight: 600;
}

/* The to-do part inside the forms */

.create-new-content {
  position: relative;
  overflow: auto;
  max-height: 381.6px;
}

.todo-content {
  width: clamp(200px, 90%, 600px);
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 5px;
  top: 20px;
  left: 10%;
}

.todo-content > div {
  display: flex;
  gap: 1ch;
  width: clamp(263px, 80%, 500px);
}

.todo-content > button {
  width: clamp(263px, 80%, 500px);
}

.todo-content > div > input {
  flex: 1;
}

.todo-content-priority {
  display: flex;
  gap: 1ch;

}

/* The project part inside the forms */

.project-content {
  width: clamp(200px, 90%, 600px);
  position: relative;
  display: flex;
  justify-content: center;
  gap: 5px;
  top: 20px;
}

/* The edit forms */

#edit-forms-window {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 9998;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
}

.edit-forms {
  display: grid;
  grid-template: 3rem 1fr / clamp(0px, 20%, 150px) auto;
  background: white;
  padding: 0;
  /* I wanted to limit the width growth, but naahh, too much work */
  min-width: 70vw;
  max-width: 800px;
  width: auto;
  min-height: 40vh;
  border-radius: 5px;
}

.edit-forms-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  grid-row: 1 / 2;
  grid-column: 1 / 3;
  background-color: rgba(250, 243, 147, 0.705);
  font-size: 1.5rem;
  font-weight: 600;
}

.edit-forms-header button {
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  font: inherit;
  color: inherit;
  cursor: pointer;
}

.edit-forms-content {
  grid-row: 2 / 3;
  grid-column: 1 / 3;
}

.dropdown-title {
  flex-grow: 1;
}

.dropdown-menu {
  flex-grow: 5;
}

/* I would, theoretically, start the todo-content part, but all this design are annoying the shit out of me, so I will jump for some JavaScript now */

/* Dynamically allocated */

.hidden {
  display: none;
  visibility: hidden;
}

.disapear-of-flux-layout {
  transform: translateX(100vw);
}

.transparent {
  color: transparent;
}

/* The dropdown */

.rotate-90{
  transform: rotate(90deg);
}

@media (max-width: 700px) {
  .content {
    display: grid;
    grid-template: 4.2rem 1fr 40px / 1fr;
  }

  .main-content {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
  }

  .sidebar {
    grid-column: 1 / 2;
    border-left: 1px solid rgb(124, 124, 124);
    transform: translateX(-100vw);
  }

  .menu-icon {
    visibility: visible;
  }

  h1 {
    left: 2.5vw;
  }
}

@media (min-width: 701px) {
  .menu-icon {
    visibility: hidden;
  }
}
