mn: 2 / 3;\n  grid-row: 2 / 3;\n}\n\n.main-content > :first-child {\n  margin-top: 2rem;\n}\n\n.todo-item {\n  display: flex;\n  flex-flow: row nowrap;\n  align-items: center;\n  width: 90%;\n  margin-left: 5%;\n  margin-right: 5%;\n  margin-bottom: 5px;\n  height: 2.3rem;\n}\n\n.todo-item:hover {\n  background-color: rgb(233, 233, 233);\n}\n\n.todo-priority-low {\n  margin-right: 5px;\n  width: 3px;\n  height: 100%;\n  background-color: var(--light-green);\n}\n\n.todo-priority-medium {\n  margin-right: 5px;\n  width: 3px;\n  height: 100%;\n  background-color: var(--yellow);\n}\n\n.todo-priority-high {\n  margin-right: 5px;\n  width: 3px;\n  height: 100%;\n  background-color: var(--light-red);\n}\n\n.todo-checkbox {\n  display: flex;\n  flex-flow: column nowrap;\n  justify-content: space-around;\n}\n\n.todo-checkbox input[type=\"checkbox\"] {\n  width: 15px;\n  height: 15px;\n  border: 1px solid black;\n  border-radius: 10px;\n  transition: box-shadow 0.3s;\n  background: white;\n}\n\n.todo-description-button {\n  margin-left: 10px;\n  margin-right: 10px;\n}\n\n.todo-description-button > img {\n  height: 20px;\n}\n\n.todo-delete-button {\n  margin-left: auto;\n}\n\n.todo-delete-button > img {\n  height: 20px;\n}\n\n.todo-title {\n  margin-right: 5px;\n}\n\n.todo-date {\n  margin-right: 20px;\n}\n\n/* SIDEBAR */\n\n.sidebar {\n  position: relative;\n  grid-column: 1 / 2;\n  grid-row: 2 / 4;\n  font-size: 22px;\n  background-color: #eee;\n  border-right: 1px solid rgb(124, 124, 124);\n  padding: 1.5rem;\n  overflow: hidden;\n}\n\n.sidebar:hover {\n  overflow: auto;\n}\n\n.sidebar {\n  scrollbar-width: none; /* Firefox */\n}\n\n.sidebar ul {\n  list-style: none;\n}\n\n.sidebar-navegation-menu ul {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-start;\n}\n\n.sidebar-navegation-menu > ul > * {\n  padding: 0.5rem 1.5rem;\n  margin-bottom: 7px;\n  cursor: pointer;\n}\n\n.fa-hashtag {\n  background-size: 0.3rem;\n  font-weight: 1;\n}\n\n.project-item {\n  padding: 0.1rem 0.8rem;\n}\n\n.project-item:hover {\n  font-weight: 600;\n}\n\n.create-todo-icon {\n  position: fixed;\n  bottom: 32px;\n  left: 45px;\n  transform: translateX(-50%);\n  font-size: 48px;\n  color: #ffe96c;\n  background-color: #000000;\n  border-radius: 50px;\n  padding: -1px;\n  cursor: pointer;\n}\n\n/* FOOTBAR */\n\nfooter {\n  grid-column: 1 / 3;\n  grid-row: 3 / 4;\n}\n\nfooter p {\n  position: relative;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  text-align: center;\n}\n\nfooter p a {\n  text-decoration: none;\n}\n\n/* Forms for new todos and projects */\n\n#create-new-window {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100vw;\n  height: 100vh;\n  z-index: 9998;\n  background-color: rgba(0, 0, 0, 0.3);\n  display: flex; /* Don't forgot to take away */\n  align-items: center;\n  justify-content: center;\n}\n\n.create-new {\n  display: grid;\n  grid-template: 3rem 1fr / clamp(0px, 20%, 150px) auto;\n  background: white;\n  padding: 0;\n  width: clamp(20vw, 600px, max(90vw, 55rem));\n  min-height: 40vh;\n  border-radius: 5px;\n}\n\n.create-new-header {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  padding: clamp(8px, 3vw, 15px);\n  grid-row: 1 / 2;\n  grid-column: 1 / 3;\n  background-color: rgba(250, 243, 147, 0.705);\n  font-size: 1.5rem;\n  font-weight: 600;\n\n  min-width: 0 !important; /* Permite que o flex container encolha */\n  width: 100%; /* Garante que use toda largura disponvel */\n  overflow: hidden;\n}\n\n.create-new-header > * {\n  min-width: 0;\n}\n\n.create-new-header p {\n  white-space: normal;\n  word-break: break-word;\n  /* ou para ellipsis quando muito pequeno */\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n  flex-shrink: 1;\n  min-width: 0; /* CRUCIAL para flex items com texto */\n}\n\n.create-new-header button {\n  background: none;\n  border: none;\n  padding: 0;\n  margin: 0;\n  font: inherit;\n  color: inherit;\n  cursor: pointer;\n}\n\n.create-new-sidebar {\n  grid-row: 2 / 3;\n  grid-column: 1 / 2;\n  padding: 1.3rem;\n  font-size: 1.2rem;\n  background-color: #eee;\n}\n\n.create-new-options {\n  display: flex;\n  flex-flow: column wrap;\n  justify-content: start;\n  align-items: start;\n  gap: 10px;\n  margin-left: 20%;\n}\n\n.create-new-options li {\n  cursor: pointer;\n}\n\n.create-new-sidebar ul {\n  list-style: none;\n}\n\n.create-new-sidebar > ul > *:hover {\n  font-weight: 600;\n}\n\n/* The to-do part inside the forms */\n\n.create-new-content {\n  position: relative;\n  overflow: auto;\n  max-height: 381.6px;\n}\n\n.todo-content {\n  width: clamp(200px, 90%, 600px);\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  gap: 5px;\n  top: 20px;\n  left: 10%;\n}\n\n.todo-content > div {\n  display: flex;\n  gap: 1ch;\n  width: clamp(263px, 80%, 500px);\n}\n\n.todo-content > button {\n  width: clamp(263px, 80%, 500px);\n}\n\n.todo-content > div > input {\n  flex: 1;\n}\n\n.todo-content-priority {\n  display: flex;\n  gap: 1ch;\n\n}\n\n/* The project part inside the forms */\n\n.project-content {\n  width: clamp(200px, 90%, 600px);\n  position: relative;\n  display: flex;\n  justify-content: center;\n  gap: 5px;\n  top: 20px;\n}\n\n/* The edit forms */\n\n#edit-forms-window {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100vw;\n  height: 100vh;\n  z-index: 9998;\n  background-color: rgba(0, 0, 0, 0.3);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.edit-forms {\n  display: grid;\n  grid-template: 3rem 1fr / clamp(0px, 20%, 150px) auto;\n  background: white;\n  padding: 0;\n  /* I wanted to limit the width growth, but naahh, too much work */\n  min-width: 70vw;\n  max-width: 800px;\n  width: auto;\n  min-height: 40vh;\n  border-radius: 5px;\n}\n\n.edit-forms-header {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  padding: 15px;\n  grid-row: 1 / 2;\n  grid-column: 1 / 3;\n  background-color: rgba(250, 243, 147, 0.705);\n  font-size: 1.5rem;\n  font-weight: 600;\n}\n\n.edit-forms-header button {\n  background: none;\n  border: none;\n  padding: 0;\n  margin: 0;\n  font: inherit;\n  color: inherit;\n  cursor: pointer;\n}\n\n.edit-forms-content {\n  grid-row: 2 / 3;\n  grid-column: 1 / 3;\n}\n\n.dropdown-title {\n  flex-grow: 1;\n}\n\n.dropdown-menu {\n  flex-grow: 5;\n}\n\n/* I would, theoretically, start the todo-content part, but all this design are annoying the shit out of me, so I will jump for some JavaScript now */\n\n/* Dynamically allocated */\n\n.hidden {\n  display: none;\n  visibility: hidden;\n}\n\n.disapear-of-flux-layout {\n  transform: translateX(100vw);\n}\n\n.transparent {\n  color: transparent;\n}\n\n/* The dropdown */\n\n.rotate-90{\n  transform: rotate(90deg);\n}\n\n@media (max-width: 700px) {\n  .content {\n    display: grid;\n    grid-template: 4.2rem 1fr 40px / 1fr;\n  }\n\n  .main-content {\n    grid-column: 1 / 2;\n    grid-row: 2 / 3;\n  }\n\n  .sidebar {\n    grid-column: 1 / 2;\n    border-left: 1px solid rgb(124, 124, 124);\n    transform: translateX(-100vw);\n  }\n\n  .menu-icon {\n    visibility: visible;\n  }\n\n  h1 {\n    left: 2.5vw;\n  }\n}\n\n@media (min-width: 701px) {\n  .menu-icon {\n    visibility: hidden;\n  }\n}\n"],"sourceRoot":""}]);
